import React from 'react';
import { motion } from 'framer-motion';

import { TransformWrapper, TransformComponent } from "react-zoom-pan-pinch";

const Modal = ({ setSelectedImg, selectedImg }) => {

  const handleClick = (e) => {
    //if (e.target.classList.contains('backdrop')) {
      document.body.style.overflow = 'unset';
      setSelectedImg(null);
    //}
  }

  return (
    <motion.div className="backdrop" onClick={handleClick}
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
    >
      <TransformWrapper 
        options={{limitToBounds:false,limitToWrapper:false}}
        pan={{paddingSize:30}}
        >
        <TransformComponent>
      <motion.img src={selectedImg.src} onClick={handleClick} alt="enlarged pic"
        initial={{ scale: 0 }}
        animate={{ scale: 1 }}
      />
      </TransformComponent>
      </TransformWrapper>
      <div className="CloseImg">X</div>
    </motion.div>
  )
}

export default Modal;