import "bootstrap/dist/css/bootstrap.min.css";
import {
  Container,
  Image,
  Form,
  Navbar,
  Nav,
  Button,
  Col,
} from "react-bootstrap";
import GeneralContainer from "./components/GeneralContainer";
import ContactFooter from "./components/ContactFooter";
import Logo from "./assets/img/Main/Logo.png";
import { useRef} from "react";
import { HashRouter, Switch, Route } from "react-router-dom";

import {Helmet} from 'react-helmet-async';
//import { Helmet } from "react-helmet";
import { AnimatePresence} from "framer-motion";

//import Fab from "./components/Fab";
import Cards from "./components/Cards";
import CardsGallery from "./components/CardsGallery";

//import Lightbox from "react-awesome-lightbox";
// You need to import the CSS only once
//import "react-awesome-lightbox/build/style.css";


function App() {
  const myRef = useRef(null);
  const executeScroll = () =>
    myRef.current.scrollIntoView({
      behavior: "smooth",
    });

    //const [commentList,setCommentList]=useState({})

  return (
    <HashRouter basename="/" className="App">
      <Helmet>
        <title>Installationstjänster Stockholm, Väsby - PetrossonBygg</title>
        <link rel="canonical" href="https://petrossonbygg.github.io"/>

        <meta
          name="description"
          content="Professionella dekoration och installationstjänster.
          dörrmontering,golvmontering,köksmontering,epoxy,vaxhouse,altanmontering,terrassmontering och renovering i Upplands Väsby, Stockholm"
        />
        <meta name="keywords" content="montering,epoxy,Petrosson Bygg,golvmontering,dörrsmontering,köksmontering,altanmontering,terrassmontering,renovering,vaxhouse,Installationstjänster Stockholm, terrassinstallation,vaxhouseinstallation , Heminredning, dekoration, installationstjänster, Upplands Väsby, Stockholms län, PetrossonBYGG, PetrossonBYGG, decoration stockholm, installation stockholm, Dörr,altan, golv, kök, installationstjänster stockholm, stockholm installationstjänster"/>
        <meta name="author" content="Boutros Tawaifi" />

        <meta property="og:site_name" content="PetrossonBygg"/>
        <meta name="title" property="og:title" content="Installationstjänster Stockholm, Väsby - PetrossonBygg"/>
        <meta property="og:url" content="https://petrossonbygg.github.io"/>
        <meta property="og:type" content="website"/>
        <meta property="og:description" content="Professionella dekoration och installationstjänster."/>
        <meta name="image" property="og:image" content="./assets/img/Logo.png" />
        <meta property="og:image:width" content="1500"/>
        <meta property="og:image:height" content="745"/>
        <meta itemprop="name" content="PetrossonBygg"/>
        <meta itemprop="url" content="https://petrossonbygg.github.io"/>

      </Helmet>

      <AnimatePresence exitBeforeEnter>
        <div>
          <Navbar bg="light" variant="light">
            
            <Image className="Logo" onClick={()=>window.location.href='/#'} src={Logo} width="105" height="35" />
            
            <Nav className="mr-auto"></Nav>
            <Form inline>
              <Button
                onClick={executeScroll}
                variant="primary"
                style={{ borderRadius: 20 }}
              >
                Contact
              </Button>
            </Form>
          </Navbar>

          <Container fluid style={{ padding: 0 }}>

            <Switch key={window.location.pathname}>
              <Route exact path="/">
              <Title containerClass={"bg"} />
                <Home />
              </Route>

              <Route exact path="/Kok">
              <Title containerClass={"bg bg_Kok"} />
              <Kok />
              </Route>

              <Route exact path="/Golv">
              <Title containerClass={"bg bg_Golv"} />
                <Golv />
              </Route>

              <Route exact path="/Dorr">
              <Title containerClass={"bg bg_Dorr"} />
                <Dorr />
              </Route>

              <Route exact path="/Terrass">
              <Title containerClass={"bg bg_Terrass"} />
                <Terrass />
              </Route>

              <Route exact path="/VaxHouse">
              <Title containerClass={"bg bg_VaxHouse"} />
                <VaxHouse />
              </Route>

              <Route exact path="/Badrum">
              <Title containerClass={"bg bg_Badrum"} />
                <Badrum />
              </Route>

              <Route exact path="/Bastu">
              <Title containerClass={"bg bg_Bastu"} />
                <Bastu />
              </Route>

              <Route exact path="/Epoxy">
              <Title containerClass={"bg bg_Epoxy"} />
                <Epoxy />
              </Route>

              {/*<Route exact path="/Test">
                <Test />
              </Route>*/}
            </Switch>
          </Container>

          <span ref={myRef}></span>
          <ContactFooter />
        </div>
      </AnimatePresence>
    </HashRouter>
  );
}
