function Title(props) {
  return (
    <GeneralContainer containerClass={props.containerClass}>
      <div className="bg_container_text ml-auto">
        <Col>
          <h1 style={{ fontFamily: "MY_futur" }}>PetrossonBYGG</h1>
          <h4 style={{ fontFamily: "MY_futurLt" }}>Professionella dekoration och installationstjänster</h4>
          <h3 style={{ fontFamily: "MY_futur" }}>Upplands Väsby, Stockholm</h3>
        </Col>
      </div>
    </GeneralContainer>
  );
}

function Home() {
  return (
    <GeneralContainer containerClass={"bg_images"}>
      <Cards />
    </GeneralContainer>
  );
}

function Kok() {
  return (
    <GeneralContainer containerClass={"bg_images"}>
      <CardsGallery setCards={"Kok"} />
    </GeneralContainer>
  );
}

function Golv() {
  return (
    <GeneralContainer containerClass={"bg_images"}>
      <CardsGallery setCards={"Golv"} />
    </GeneralContainer>
  );
}

function Dorr() {
  return (
    <GeneralContainer containerClass={"bg_images"}>
      <CardsGallery setCards={"Dorr"} />
    </GeneralContainer>
  );
}

function Terrass() {
  return (
    <GeneralContainer containerClass={"bg_images"}>
      <CardsGallery setCards={"Terrass"} />
    </GeneralContainer>
  );
}

function VaxHouse() {
  return (
    <GeneralContainer containerClass={"bg_images"}>
      <CardsGallery setCards={"VaxHouse"} />
    </GeneralContainer>
  );
}

function Badrum() {
  return (
    <GeneralContainer containerClass={"bg_images"}>
      <CardsGallery setCards={"Badrum"} />
    </GeneralContainer>
  );
}

function Bastu() {
  return (
    <GeneralContainer containerClass={"bg_images"}>
      <CardsGallery setCards={"Bastu"} />
    </GeneralContainer>
  );
}

function Epoxy() {
  return (
    <GeneralContainer containerClass={"bg_images"}>
      <CardsGallery setCards={"Epoxy"} />
    </GeneralContainer>
  );
}

